<template>
  <div :class="$root.themeHomeChange === '1' ? 'defectView_light' : 'defectView'">
    <div class="header">
      <!-- <span class="left">监督计划</span> -->
      <div class="left">
        <span v-if="type === '我的新增'">
          <el-button
            type="primary"
            icon="el-icon-edit-outline"
            @click="planEntry(true)"
          > 计划录入 </el-button>
          <el-button
            type="warning"
            icon="el-icon-document"
            :plain="$root.themeHomeChange === '1'"
            @click="planEntry(false)"
            :disabled="!(currentRow && currentRow.id && currentRow.status === 0)"
          > 编辑 </el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            :plain="$root.themeHomeChange === '1'"
            :disabled="!(currentRow && currentRow.id)"
            @click="deleteTableItem"
          > 删除 </el-button>
        </span>
        <span v-else>
          <el-button
            type="success"
            icon="el-icon-s-check"
            :plain="$root.themeHomeChange === '1'"
            @click="openAudit"
            :disabled="multipleSelection.length === 0"
          > 审核 </el-button>
        </span>
      </div>
      <div class="right">
        <el-date-picker
          v-model="params.year"
          type="year"
          placeholder="选择年"
          style="margin-right: 15px;"
          value-format="yyyy"
          @change="queryTableList(true)"
        >
        </el-date-picker>
      </div>
    </div>
    <el-form
      :inline="true"
      class="selectBox"
      :rules="rules"
      :model="params">
      <!-- <el-button type="primary" style="float: left;">生成报告</el-button> -->
      <span>
        <el-form-item label="计划时间: " prop="planDate">
          <el-input-number
            v-model="params.jhsjBegin"
            style="width: 120px; margin-right: 15px;"
            :min="1"
            :max="12"
            size="mini"
            placeholder="开始时间"
          ></el-input-number>
          <el-input-number
            v-model="params.jhsjEnd"
            style="width: 120px;"
            :min="1"
            :max="12"
            size="mini"
            placeholder="结束时间"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="监督类别: ">
          <el-select
            size="mini"
            v-model="params.type"
            placeholder="请选择"
            style="width: 150px;"
            clearable
          >
            <el-option
              v-for="(item, index) in formOption['supervisionType']"
              :label="item.label"
              :value="item.value"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单位名称: ">
          <SelectTree
            style="width: 100%"
            ref="selectTreeRef"
            :props="defaultProps"
            :options="treeData"
            :value="valueId"
            :clearable="true"
            :accordion="true"
            @getValue="getValue($event)"
          />
        </el-form-item>
      </span>
      <span>
        <el-form-item>
          <bda-search-input
            v-model="params.keyword"
            placeholder="请输入关键字"
            @search="queryTableList(true)"
            @clear="queryTableList(true)"
            size="mini"
          >
          </bda-search-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="queryTableList(true)"
          > 查询 </el-button>
        </el-form-item>
      </span>
    </el-form>
    <div class="content">
      <el-table
        :data="tableData"
        style="width: 100%"
        :height="tableHeight"
        v-loading="tableLoading"
        :highlight-current-row="type === '我的新增'"
        @current-change="handleCurrentChange_table"
        @selection-change="handleSelectionChange"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <span v-if="type === '我的审核'">
          <el-table-column
            align="center"
            type="selection"
            width="55">
          </el-table-column>
        </span>
        <el-table-column
          type="index"
          label="序号"
          width="60"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="jdxmmc"
          label="监督项目名称"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jdnr"
          label="监督内容"
          width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="type"
          label="监督类别"
          width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jdly"
          label="监督领域"
          width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="dwmc"
          label="单位名称"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="phbm"
          label="配合部门"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="nd"
          label="年度"
          width="80"
          align="center">
        </el-table-column>
        <el-table-column
          prop="planTime"
          label="计划开展时间"
          width="100"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            {{ scope.row.jhsjBegin + '月' + ' ~ ' + scope.row.jhsjEnd + '月' }}
          </template>
        </el-table-column>
        <el-table-column
          prop="jcdx"
          label="检查对象"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="kzfs"
          label="开展方式"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="wcbz"
          label="完成标志"
          width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="remark"
          label="备注"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="user"
          label="录入人"
          width="90"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="date"
          label="录入时间"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          width="90"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <span :style="{ 'color': formOption['statusList'][1].color, 'font-weight': 'bold' }">
              {{ formOption['statusList'][scope.row.status].label }}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="auditFeedback"
          label="审核反馈"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <span v-if="type === '我的审核'">
          <el-table-column
            prop="auditName"
            label="审核人"
            width="100"
            align="center"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="auditTime"
            label="审核时间"
            width="150"
            align="center"
            show-overflow-tooltip>
            <template slot-scope="scope">
              {{ formatDate( scope.row.auditTime, 'YYYY-MM-DD HH:mm:ss' ) }}
            </template>
          </el-table-column>
        </span>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.currentPage"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="params.pageSize"
        background
        layout="prev, pager, next, sizes, jumper, total"
        :total="params.total">
      </el-pagination>
    </div>
    <planEntry v-if="showAdd" @closeDilag="init" :formData_in="currentRow" ></planEntry>
    <auditDialog
      v-if="showAudit"
      @closeDilag="init"
      :infoList="multipleSelection.map( item => {
        return {
          id: item.id,
          label: item.jdxmmc
        };
      })"
    ></auditDialog>
  </div>
</template>

<script>
  import moment from "moment";
  import planEntry from "./component/planEntry";
  import auditDialog from "./component/auditDialog";
  import Interface from "@/base/mixin/risk-control-center.js";
  import SelectTree from "@/views/components/orgTreeSelect";
  export default {
    name: "defectView",
    mixins: [Interface],
    props: {
      type: {
        type: String,
        default: () => {
          return '我的新增'
        }
      }
    },
    components: { planEntry, SelectTree, auditDialog },
    data(){
      var check_planDate = (rule, value, callback) => {
        if(!this.params.jhsjBegin && !this.params.jhsjEnd) {
          return callback();
        }
        if (this.params.jhsjBegin && this.params.jhsjEnd) {
          if(this.params.jhsjBegin > this.params.jhsjEnd) {
            return callback(new Error('计划开始时间不得大于计划结束时间'));
          } else {
            return callback();
          }
        } else {
          return callback(new Error('请填写完整计划开展时间'));
        }
      };
      return {
        // 单位选择 变量
        defaultProps: {
          children: "list",
          label: "orgName",
          value: "orgId",
        },
        treeData: [],
        valueId: '',
        
        tableLoading: false,
        tableHeight: window.innerHeight - 360,
        departmentList: [],
        params: {
          pageSize: 10,
          currentPage: 1,
          total: 0,
          year: '',
          unit: ''
        },
        rules: {
          planDate: [
            { required: false, validator: check_planDate, trigger: 'blur' }
          ]
        },
        showAdd: false,
        showAudit: false,
        multipleSelection: [],
        formOption: {
          supervisionType: [
            {
              label: '职能监督',
              value: '职能监督'
            },
            {
              label: '专业监督',
              value: '专业监督'
            },
            {
              label: '专责监督',
              value: '专责监督'
            },
            {
              label: '安全监督',
              value: '安全监督'
            },
            {
              label: '基层单位日常监督',
              value: '基层单位日常监督'
            }
          ],
          leadDepartment: [
            {
              label: '全部',
              value: '全部'
            },
            {
              label: '市场营销域',
              value: '市场营销域'
            },
            {
              label: '计划生产域',
              value: '计划生产域'
            }
          ],
          statusList: {
            0: {
              label: '待审核',
              color: '#55aaff'
            },
            1: {
              label: '审核通过',
              color: '#6bcc6b'
            },
            2: {
              label: '审核不通过',
              color: '#ff4a4a'
            },
          }
        },
        tableData: [],
        currentRow: {}
      }
    },
    created() {
      this.init();
    },
    // 监听
    watch: {},
    mounted() {},
    methods: {
      init() {
        this.currentRow = {};
        this.showAdd = false;
        this.showAudit = false;
        //获取年份
        this.params.year = new Date().getFullYear().toString();
        this.queryTableList(true);
        this.getOrgData();
      },
      // 表格多选
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      // 表格单选
      handleCurrentChange_table(val) {
        this.getSupervisionDetails({ id: val.id }).then((rs)=>{
          window.console.log(rs)
          if(rs && rs.id) {
            this.currentRow = rs;
          } else {
            this.currentRow = val;
          }
        }).finally(()=>{})
      },
      // 格式化数据
      formatDate(time, type) {
        return time ? moment(time).format(type) : ' / ';
      },
      // 查询单位名称
      getOrgData() {
        this.getSupervisionUnitTree().then((rs)=>{
          // window.console.log(rs)
          if(Array.isArray(rs) && rs.length !== 0) {
            // window.console.log(rs)
            this.treeData = rs;
          } else {
            this.treeData = [];
          }
        }).finally(()=>{});
      },
      // 查询表格内容
      queryTableList(reset) {
        if(reset) {
          this.setPages();
        }
        let params = {
          begin: this.params.jhsjBegin,
          end: this.params.jhsjEnd,
          current: this.params.currentPage,
          size: this.params.pageSize,
          keyword: this.params.keyword,
          type:  this.params.type,
          unit: this.params.unit,
          year: this.params.year,
          status: this.type === '我的新增' ? '' : 0
        };
        this.tableData = [];
        this.tableLoading = true;
        this.getSupervisionList(params).then((rs)=>{
          window.console.log(rs)
          if(rs) {
            this.tableData = rs.rows;
            this.setPages(rs.current, rs.size, rs.records);
          } else {
            this.setPages();
          }
        }).finally(()=>{
          this.tableLoading = false;
        })
      },
      // 设置页码
      setPages(currentPage = 1, pageSize = 10, total = 0) {
        this.params['pageSize'] = pageSize;
        this.params['currentPage'] = currentPage;
        this.params['total'] = total;
      },
      // 表格操作按钮点击
      handleClick(row, type) {
        window.console.log(`类型：${type} , 行内容:`, row);
        this.$router.push({
          path: '/details',
          query: {
            index: '4-2',
            params: window.btoa(window.encodeURIComponent(JSON.stringify({
              name: row.name,
              orderId: row.orderId || this.$route.query.orderId,
            }))),
            actNav: this.$route.query.actNav
          }
        });
      },
      handleSizeChange(val) {
        // window.console.log(`每页 ${val} 条`);
        this.params.pageSize = val;
        this.queryTableList();
      },
      handleCurrentChange(val) {
        // window.console.log(`当前页: ${val}`);
        this.params.currentPage = val;
        this.queryTableList();
      },
      // 取值
      getValue(item){
        // window.console.log(item)
        this.params.unit = item[1];
        this.queryTableList(true)
      },
      // 计划录入
      planEntry(isEmpty = false) {
        // this.$message.success('计划录入');
        if(isEmpty) {
          this.currentRow = {};
        }
        this.showAdd = true;
      },
      // 打开审核弹窗
      openAudit() {
        this.showAudit = true;
      },
      // 删除列表选项
      deleteTableItem() {
        this.$confirm(`此操作将删除选中的监督计划, 是否继续?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // window.console.log('监督计划录入表单', this.form);
          this.supervisionDelete({ id: this.currentRow.id }).then(()=>{
            this.$message.success('删除成功');
          }).finally(()=>{
            this.init();
          });
        }).catch(() => {});
      },
    }
  }
</script>

<style scoped lang="scss">
  .defectView {
    width: auto;
    height: calc(100% - 20px);
    padding: 5px 15px 15px 15px;
    // background: linear-gradient(
    //   left, #041338 0%,
    //   rgba(40, 73, 152, 0.4) 35%,
    //   rgba(40, 73, 152, 0.6) 50%,
    //   rgba(40, 73, 152, 0.4) 65%,
    //   #041338 100%
    // );
    /deep/ {
      .el-input-number__decrease, .el-input-number__increase {
        background-color: #041338;
        border-color: #4298F3;
        color: #fff;
      }
      // 表格勾选框样式
      .el-checkbox__input, .el-checkbox__input.is-indeterminate, .el-checkbox__input.is-checked {
        .el-checkbox__inner {
          background-color: #041338;
