        }
      }
      .el-button {
        border-radius: 5px!important;
      }
      // 修改表格操作按钮样式
      .el-button--text {
        font-family: MicrosoftYaHei;
        font-size: 10px;
        color: #66B9FF;
        font-weight: 400;
      }
      .el-form {
        .el-form-item {
          // margin-bottom: 0px;
        }
        input::-webkit-input-placeholder {
          color: #4298F3;
        }
         
        input::-moz-placeholder {
          /* Mozilla Firefox 19+ */
          color: #4298F3;
        }
               
        input:-moz-placeholder {
          /* Mozilla Firefox 4 to 18 */
          color: #4298F3;
        }
               
        input:-ms-input-placeholder {
          /* Internet Explorer 10-11 */
          color: #4298F3;
        }
      }
      
      // 修改按钮样式
      .el-button--primary {
        // color: #FFF;
        // background: rgba($color: #409EFF, $alpha: .8);
        // border: 1px solid rgba($color: #409EFF, $alpha: 1);
        border-radius: 5px;
      }
      .el-button--info {
        color: #666;
        background: rgba($color: #eee, $alpha: 1);
        border: 1px solid rgba($color: #eee, $alpha: 1);
