      }
      
      .el-table__cell {
        border-left:  1px solid rgba(0, 0, 0, 1.0);
        border-right:  -1px solid rgba(0, 0, 0, 1.0);
      }
      // 覆盖element表格背景
      .el-table {
        border:  1px solid rgba(0, 0, 0, 1.0);
        background: rgba(21, 56, 117, 0.8);
        tr {
          background: rgba(34, 81, 156, 1.0);
          // 修改字体
          td {
            .cell {
              color: #d9d9d9;
              font-size: 10px;
              font-weight: 500;
            }
          } 
          th {
            .cell {
              font-family: MicrosoftYaHei-Bold;
              font-size: 12px;
              color: #ffffff;
              font-weight: 700;
              padding: 6px 0;
            }
          }
        }
        th {
          background: rgba(0, 0, 0, 0);
        }
        .warning-row {
          background: rgba(71, 42, 77, 0.7)!important;
        }
        .el-table__body {
          tr {
            height: 40px;
            background: rgba(21, 56, 117, 0.8);
            position: relative;
            padding: 5px 0;
            margin: 10px 0;
          }
        }
      }
      
      // 修改框线
      .el-table td,.building-top .el-table th.is-leaf {
        padding: 5px 0;
        // border-top:  2px solid rgba(0, 0, 0, 1.0);
        border-bottom:  1px solid rgba(0, 0, 0, 1.0);
      }
      .el-table::before {
        // border-bottom:  1px solid #0065B3;
        height: 0px
      }
      // 设置表格头部边框
      .el-table th.is-leaf {
        /* 去除上边框 */
          // border: none;
          // border:  1px solid rgba(0, 0, 0, 1.0);
          border-bottom: 1px solid rgba(0, 0, 0, 1.0);
      }
      
      // 修改鼠标悬浮
      .el-table--enable-row-hover .el-table__body tr:hover>td {
        background-color: rgba(0, 95, 168, 0.5);
      }
      
      // 单选高亮
      .el-table__body tr.current-row>td {
        background-color: rgba(0, 95, 168, 0.5)!important;
      }
      
      // 修改分页组件样式
      .el-pagination {
        margin-top: 10px;
        text-align: center;
        color: #fff;
        .btn-next, .btn-prev,
        .el-pager li,
        .el-select .el-input .el-input__inner,
        .el-pagination__editor.el-input .el-input__inner {
          background-color: #041338;
          color: #d3d3d3;
          border: 1px solid #2a5cb0;
          border-radius: 3px;
        }
        .el-pager li:not(.disabled).active {
          background-color: #4389F9;
        }
        .el-pagination__jump, .el-pagination__total {
          color: #fff;
          margin-left: 10px;
        }
      }
      
      .el-picker-panel {
        background: rgba(0,0,0,0);
      }
    }
    .header {
      width: calc(100%);
      text-align: left;
      font-family: MicrosoftYaHei-Bold;
      font-size: 14px;
      color: #FFFFFF;
      font-weight: bold;
      // border-left: 4px solid #4298F3;
      padding: 2px 0;
      margin: 15px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left {
        width: calc(100% - 300px);
        text-align: left;
      }
      .right {
        width: calc(300px);
        text-align: right;
      }
    }
    .selectBox {
      min-height: 40px;
      margin: 5px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      /deep/ .el-input__icon {
        height: auto!important;
        color: #4298F3;
      }
      /deep/ .el-input__inner {
        background-color: #041338;
        color: #4298F3;
        border: 1px solid #2a5cb0;
        border-radius: 3px;
        text-align: left;
      }
      /deep/ .el-form-item__label {
        color: #FFF
      }
      
      // 修改时间组件样式
      /deep/ .el-range-editor.el-input__inner {
        background-color: #041338;
        border: 1px solid #509be1;
        border-radius: 5px;
        .el-range-input, .el-range-separator {
          background-color: #041338;
          color: #FFF;
        }
      }
    }
    .content {
      width: 100%;
    }
  }
  
  // 浅色主题
  .defectView_light {
    width: auto;
    height: calc(100% - 20px);
    padding: 5px 15px 15px 15px;
    /deep/ {
      // 表格勾选框样式
      .el-checkbox__input, .el-checkbox__input.is-indeterminate, .el-checkbox__input .is-checked {
        .el-checkbox__inner {
          background-color: #fff;
          border: 1px solid #bbb;
          border-radius: 3px;
          text-align: center;
          &::after {
            border-color: #333;
          }
        }
      }
