<template>
  <div :class="$root.themeHomeChange === '1' ? 'auditDialog_light' : 'auditDialog'">
    <div class="auditDialog-con">
      <div class="headBox">
        <span class="title"> 系统审核 </span>
        <bda-button @click="goBack">返回</bda-button>
      </div>
      <div class="content">
        <el-form
          :model="form"
          :rules="rules"
          ref="form"
          label-width="150px"
          label-suffix="："
          label-position="left"
          v-loading="formLoading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
        >
          <el-row :gutter="50">
            <el-col :span="24">
              <bda-form-item label="系统审核" prop="status">
              <el-radio-group v-model="form.status">
                <el-radio :label="1">审核通过</el-radio>
                <el-radio :label="2">审核不通过</el-radio>
              </el-radio-group>
              </bda-form-item>
            </el-col>
            <el-col :span="24" style="height: 150px;">
              <bda-form-item label="审核反馈" prop="auditFeedBack">
                <el-input
                  resize="none"
                  type="textarea"
                  :autosize="{ minRows: 6, maxRows: 6}"
                  placeholder="请输入审核反馈"
                  v-model="form.auditFeedBack">
                </el-input>
              </bda-form-item>
            </el-col>
          </el-row>
        </el-form>
        <div class="submit" >
          <el-button @click="submit()" type="primary"> 提交 </el-button>
          <el-button @click="goBack()"> 取消 </el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from "@/base/mixin/risk-control-center.js";
// import qs from "qs";
// import moment from "moment";

export default {
  components: {},
  mixins: [Interface],
  props: {
    infoList: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      //form
      form: {},
      
      // 表单加载
      formLoading: false,
      pageInfoConfig: {
        page: 1,
        rows: 20,
        total: 0,
      }, //表格-分页初始配置
      
      rules: {
        status: [
          { required: true, message: '请选择系统审核', trigger: 'change' }
        ],
        auditFeedBack: [
          { required: true, message: '请输入审核反馈', trigger: 'blur' }
        ]
      },
    };
  },
  created() {
  },
  mounted() {},
  watch: {},
  methods: {
    // 提交表单
    submit() {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.$confirm(`此操作将审核所选：${ this.infoList.map( item => item.label).join('、') } 等监督计划, 是否继续?`, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.formLoading = true;
            // window.console.log('监督计划录入表单', this.form);
            this.supervisionAudit({
                ids: this.infoList.map( item => item.id ),
                status: this.form.status,
                auditFeedBack: this.form.auditFeedBack
              }).then(()=>{
              this.$message.success('提交成功');
              this.goBack();
            }).finally(()=>{
              this.formLoading = false;
            });
          }).catch(() => {});
        } else {
          return false;
        }
      });
    },
    // 返回页面
    goBack() {
      this.$emit('closeDilag');
    }
  },
};
</script>

<style lang="scss" scoped>
.auditDialog {
  width: 100%;
  height: calc(100%);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  background-color: rgba($color: #333, $alpha: .5);
  z-index: 999;
  .auditDialog-con {
    min-width: 600px;
    max-width: 1000px;
    height: 400px;
    width: 60%;
    border-radius: 10px;
    position: relative;
    margin: auto;
    padding: 0 10px;
    border-left: 1px solid #4389f9;
    border-right: 1px solid #4389f9;
    border-bottom: 1px solid #4389f9;
    background: rgba($color: #041338, $alpha: .9);
    .headBox {
      width: calc(100%);
      height: 35px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px;
      margin-left: -10px;
      background-color: rgba($color: #4389f9, $alpha: .2);
      border-radius: 10px 10px 0 0;
      // border-bottom: 1px solid #ccc;
      .title {
        font-size: 16px;
        font-weight: bold;
        color: #eee;
        display: block;
        padding: 5px 10px;
        border-left: 4px solid #4389f9;
      }
    }
    .content {
      width: calc(100% - 30px);
      height: calc(95% - 61px);
      padding: 10px 25px;
      // margin-top: 10px;
      margin-left: -10px;
      border-top: 1px solid #4389f9;
      position: relative;
      .submit {
        position: absolute;
        right: 15px;
        bottom: 15px;
        text-align: right;
      }
      /deep/ {
        .el-input-number__decrease, .el-input-number__increase {
          background-color: #041338;
          border-color: #4298F3;
          color: #fff;
        }
        // 按钮样式
        .el-button--default {
          background-color: #041338;
          color: #4298F3;
          border: 1px solid #2a5cb0;
          border-radius: 3px;
          padding: 8px 25px;
        }
        .el-button--primary {
          background-color: #4389F9;
          color: #ffffff;
          border-radius: 3px;
          text-align: center;
          transition: all .3s;
          padding: 8px 25px;
          &:hover {
            color: #4298F3;
            background-color: #041338;
            border: 1px solid #2a5cb0;
          }
        }
        .el-form {
          margin-bottom: 10px;
          .el-row {
            // height: 32px;
            .el-col {
              height: 62px;
              margin-bottom: 13px;
              padding-top: 32px;
              position: relative;
              .el-form-item__label {
                position: absolute;
                top: 0;
                left: 25px;
                color: #fff;
              }
              .el-form-item__content {
                margin-left: 0!important;
                .bda-date-picker {
                  width: 100%;
                }
              }
              .el-input__inner {
                background: #041338;
                border: 1px solid #4389f9;
                border-radius: 4px;
                color: #4298f3;
              }
              
              .el-input__inner, .el-textarea__inner, .el-upload--picture-card{
                background-color: #041338;
                color: #4298F3;
                border: 1px solid #2a5cb0;
                border-radius: 3px;
                text-align: left;
                &::placeholder {
                color: #4298f3 !important;
              }
              }
        
              .el-date-editor {
                width: 100%;
        
                .el-range-input {
                  background-color: #041338;
                  color: #4298f3;
                  &::placeholder {
                    color: #4298f3;
                  }
                }
                .el-range-separator {
                  color: #fff;
                }
        
                .el-input__icon,
                .el-input__suffix {
                  background: rgba($color: #041338, $alpha: 0) !important;
                }
              }
            }
          }
        }
      }
    }
  }
}

// 浅色主题
