              .el-input__inner, .el-textarea__inner, .el-upload--picture-card{
                background: #fff;
                border: 1px solid #bbb;
                border-radius: 4px;
                color: #333;
                text-align: left;
                &::placeholder {
                  color: #bbb !important;
                }
              }
        
              .el-date-editor {
                width: 100%;
        
                .el-range-input {
                  background-color: #fff;
                  color: #333;
                  &::placeholder {
                    color: #bbb;
                  }
                }
                .el-range-separator {
                  color: #fff;
                }
        
                .el-input__icon,
